@font-face {
  font-family: "PT Mono";
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

html {
  box-sizing: border-box;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0 none;
  clip-path: inset(100%);
}

.accordion__button {
  display: none;
}

.button {
  padding: 14px 28px;
  min-width: max-content;
  font-family: "Roboto Condensed","Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  border: 1px solid #ffffff;
  border-radius: 5px;
}

.button:visited {
  color: #ffffff;
}

.button:hover {
  border-color: #00b2ff;
}

.button:active {
  opacity: 0.5;
}

.contacts-link {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  list-style: none;
}

.contacts-link__item {
  min-width: max-content;
  font-family: "PT Mono", "Nimbus Mono L", "Courier New", monospace;
  font-size: 16px;
  line-height: 18px;
  color: #ffffff;
}

.contacts-link__item:not(:last-child) {
  margin-right: 88px;
}

.contacts-link__item a {
  display: flex;
  align-items: center;
  color: inherit;
}

.contacts-link__item a svg {
  stroke: #00b2ff;
}

.contacts-link__item a:visited {
  color: inherit;
}

.contacts-link__item a:hover {
  color: #00b2ff;
}

.contacts-link__item a:hover svg {
  stroke: #ffffff;
}

.contacts-link__item a:active {
  opacity: 0.5;
}

.contacts-link__item--mail svg {
  margin-right: 20px;
  width: 19px;
  height: 14px;
}

.contacts-link__item--telephone svg {
  margin-right: 16px;
  width: 18px;
  height: 18px;
}

.contacts {
  font-size: 15px;
  line-height: 25px;
}

.contacts p {
  margin-top: 0;
  margin-bottom: 0;
  opacity: 0.5;
}

.contacts a {
  display: block;
}

.navigation {
  display: grid;
  grid-template-rows: repeat(4, min-content);
  grid-auto-columns: max-content;
  grid-auto-flow: column;
  font-size: 15px;
  line-height: 25px;
  list-style: none;
  column-gap: 16%;
}

.navigation a {
  color: inherit;
  opacity: 0.5;
}

.navigation a:visited {
  color: inherit;
}

.navigation a:hover {
  color: #00b2ff;
}

.navigation a:active {
  color: inherit;
  opacity: 1;
}

.navigation__column-list {
  padding-left: 0;
  list-style: none;
}

.navigation__item {
  min-width: max-content;
}

.page-body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  margin: 0 auto;
  min-height: 100%;
}

.page-body--opened-menu {
  overflow: hidden;
}

.page-footer {
  font-family: "PT Mono", "Nimbus Mono L", "Courier New", monospace;
  color: #1b2d37;
}

.page-footer h2 {
  margin-top: 0;
  margin-bottom: 26px;
  font-family: "Roboto Condensed","Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

.page-footer a:not(.page-footer__logo) {
  color: inherit;
  opacity: 0.5;
}

.page-footer a:not(.page-footer__logo):visited {
  color: inherit;
}

.page-footer a:not(.page-footer__logo):hover {
  color: #00b2ff;
}

.page-footer a:not(.page-footer__logo):active {
  color: inherit;
  opacity: 1;
}

.page-footer__top-container {
  margin-bottom: 15px;
  padding-bottom: 55px;
  width: 100%;
  border-bottom: 1px solid rgba(98, 105, 98, 0.2);
}

.page-footer__container {
  display: grid;
  grid-template-columns: 223px 1fr 333px;
  margin: 0 auto;
  padding-top: 65px;
  padding-left: 30px;
  padding-right: 30px;
  max-width: 1470px;
}

.page-footer__logo {
  display: block;
  margin-bottom: 48px;
}

.page-footer__logo-image {
  margin-right: 7px;
  width: 52px;
  height: 52px;
}

.page-footer__logo-title {
  width: 148px;
  height: 45px;
}

.page-footer__copyright-top {
  display: none;
}

.page-footer__social {
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 0;
  padding-left: 0;
}

.page-footer__navigation {
  display: flex;
  justify-content: center;
}

.page-footer__navigation-content {
  min-width: 54%;
}

.page-footer__navigation-list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

.page-footer__bottom-container {
  display: grid;
  grid-template-columns: max-content 1fr max-content;
  column-gap: 10%;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
  max-width: 1470px;
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}

.page-footer__bottom-container p {
  margin-top: 0;
  margin-bottom: 0;
  min-width: max-content;
  opacity: 0.5;
}

.page-footer__copyright {
  margin-right: 59px;
}

.page-footer__privacy {
  width: max-content;
}

.page-footer__developer {
  min-width: max-content;
}

.page-footer__accordion-button {
  display: none;
}

.page-header {
  padding-top: 33px;
  background-color: #1b2d37;
}

.page-header__logo {
  margin-right: 30px;
}

.page-header__logo-image {
  margin-right: 10px;
  width: 52px;
  height: 52px;
}

.page-header__logo-title {
  width: 134px;
  height: 45px;
}

.page-header__link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
  max-width: 1470px;
}

.page-header__contacts {
  margin-top: 0;
  margin-right: 50px;
  margin-bottom: 0;
  padding-left: 0;
  flex-grow: 1;
}

.social {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  list-style: none;
}

.social a {
  display: block;
  fill: #999999;
}

.social a:visited {
  fill: #999999;
}

.social a:hover {
  fill: #00b2ff;
}

.social a:active {
  fill: #999999;
  opacity: 0.5;
}

.social__item:not(:last-child) {
  margin-right: 15px;
}

@media (max-width: 1100px) {
  .navigation {
    column-gap: 5%;
  }

  .page-footer__navigation-content {
    width: 100%;
  }

  .page-footer__bottom-container {
    column-gap: 5%;
  }
}

@media (max-width: 1023px) {
  .contacts-link {
    align-items: center;
  }

  .contacts-link__item:not(:last-child) {
    margin-right: 33px;
  }

  .contacts-link__item span {
    display: none;
  }

  .contacts-link__item--mail svg {
    margin-right: 0;
    width: 25px;
    height: 19px;
  }

  .contacts-link__item--telephone svg {
    margin-right: 0;
    width: 24px;
    height: 24px;
  }

  .navigation {
    display: block;
  }

  .page-footer__top-container {
    padding-bottom: 2px;
  }

  .page-footer__container {
    grid-template-columns: max-content 1fr max-content;
    margin-bottom: 33px;
    padding-top: 19px;
    padding-left: 0;
    padding-right: 0;
  }

  .page-footer__link-container {
    margin-bottom: 40px;
    padding-bottom: 18px;
    padding-left: 25px;
    padding-right: 25px;
    display: flex;
    justify-content: space-between;
    grid-column: 1/-1;
    border-bottom: 1px solid rgba(98, 105, 98, 0.2);
  }

  .page-footer__logo {
    margin-bottom: 0;
  }

  .page-footer__copyright-top {
    display: block;
    margin-top: 0;
    margin-bottom: 0;
    padding-top: 16px;
    font-size: 13px;
    line-height: 24px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    opacity: 0.5;
  }

  .page-footer__social {
    padding-top: 11px;
  }

  .page-footer__navigation {
    padding-left: 25px;
  }

  .page-footer__contacts {
    grid-column-start: 3;
    padding-right: 34px;
  }

  .page-footer__bottom-container {
    display: flex;
    justify-content: space-between;
    padding-top: 8px;
    padding-left: 25px;
    padding-bottom: 25px;
  }

  .page-footer__bottom-container p {
    display: none;
  }

  .page-footer__copyright {
    display: none;
  }

  .page-header {
    padding-top: 20px;
  }

  .page-header__link {
    padding-left: 25px;
    padding-right: 25px;
  }

  .page-header__contacts {
    margin-right: 0;
  }

  .page-header__order-call {
    display: none;
  }
}

@media (max-width: 767px) {
  .accordion {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 73px;
  }

  .accordion__button {
    display: block;
    width: 16px;
    height: 16px;
    border: none;
    background-color: transparent;
    cursor: pointer;
    stroke: #cdcdcd;
  }

  .accordion__button--current {
    width: 16px;
    height: 1px;
    background-color: #cdcdcd;
  }

  .accordion__button--current svg {
    display: none;
  }

  .contacts-link__item:not(:last-child) {
    margin-right: 23px;
  }

  .page-footer__top-container {
    border-style: none;
  }

  .page-footer__container {
    display: block;
    padding-top: 46px;
  }

  .page-footer__link-container {
    padding-left: 18px;
    padding-right: 18px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .page-footer__logo-image {
    width: 38px;
    height: 38px;
  }

  .page-footer__bottom-container {
    flex-direction: column;
    justify-content: center;
    padding-left: 18px;
    padding-right: 18px;
  }

  .page-footer__accordion-section {
    position: relative;
    padding-left: 18px;
    padding-right: 18px;
    border-bottom: 1px solid rgba(98, 105, 98, 0.2);
  }

  .page-footer__accordion-section h2 {
    height: 73px;
  }

  .page-footer__accordion-button {
    position: absolute;
    top: 0;
    right: 5px;
    display: block;
    width: 16px;
    height: 16px;
    border: none;
    background-color: transparent;
    cursor: pointer;
    stroke: #cdcdcd;
  }

  .page-footer__accordion-button--current {
    width: 16px;
    height: 1px;
    background-color: #cdcdcd;
  }

  .page-footer__accordion-button--current svg {
    display: none;
  }

  .page-footer__accordion-content--hidden {
    display: none;
  }

  .page-header {
    padding-top: 17px;
  }

  .page-header__logo-image {
    width: 38px;
    height: 38px;
  }

  .page-header__logo-title {
    display: none;
  }

  .page-header__link {
    padding-left: 17px;
    padding-right: 17px;
  }
}